import React, { useState} from 'react'
import { 
    MyTabs, 
    MyTab, 
    MyAvatar, 
    ContentDiv 
} from './Styled'
import HomeIcon from '@material-ui/icons/Home';
import GitHubIcon from '@material-ui/icons/GitHub';
import FolderIcon from '@material-ui/icons/Folder';
import Home from './Home';
import Github from './Github';
import Contact from './Contact';

// TO DO: get github tab filled out
// maybe fix contact info
// make home content more homey
// make sure this works on mobile (maybe)
// dude, remove that white sliver (i thought this shit was 100vw wtf)

const Content = () => {

    const [value, setValue] = useState(0)

    const handleChange = (e, newValue) => {
        setValue(newValue)
    }
    return (
        <div>
            <ContentDiv style={{width: '90%'}}>
                <MyAvatar alt='Andrew Hua' src="https://github.com/andrewhua92/andrew-web-app/blob/8df3ec58e5b4f31da225f8a0328bb7f871ed8234/public/my_face.JPG"/>
                <MyTabs
                    value={value}
                    onChange={handleChange}
                > 
                    <MyTab 
                        icon={<HomeIcon/>}
                    />
                    <MyTab 
                        icon={<GitHubIcon/>}
                    />
                    <MyTab 
                        icon={<FolderIcon/>}
                    />
                </MyTabs>
            </ContentDiv>
            <ContentDiv style={{marginLeft: "250px"}}>
                {value === 0 ? 
                <Home/>: (value === 1 ? 
                <Github/> : 
                <Contact/>)
                }
            </ContentDiv>
        </div>
    )
}

export default Content